.owl-nav .owl-prev,
.owl-nav .owl-next {
  position: absolute;
  width: var(--amotos-owl-nav-width);
  height: var(--amotos-owl-nav-height);
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  background-color: var(--amotos-owl-nav-bg-color);
  color: var(--amotos-owl-nav-color);
  text-align: center;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
  cursor: pointer;
}
.owl-nav .owl-prev i,
.owl-nav .owl-next i {
  font-size: var(--amotos-owl-nav-icon-font-size);
  font-weight: var(--amotos-owl-nav-icon-font-weight);
  font-family: var(--amotos-owl-nav-icon-font-family);
}
.owl-nav .owl-prev:after,
.owl-nav .owl-next:after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.owl-nav .owl-prev:hover,
.owl-nav .owl-next:hover {
  background-color: var(--amotos-color-accent);
  color: var(--amotos-color-accent-foreground);
}
.owl-nav .owl-prev {
  left: 0;
}
.owl-nav .owl-prev:after {
  border-bottom: solid 0 transparent;
  border-top: solid var(--amotos-owl-nav-height) var(--amotos-owl-nav-bg-color);
  border-right: solid 10px transparent;
  left: 100%;
}
.owl-nav .owl-prev:hover:after {
  border-top-color: var(--amotos-color-accent);
}
.owl-nav .owl-next {
  right: 0;
}
.owl-nav .owl-next:after {
  border-bottom: solid var(--amotos-owl-nav-height) var(--amotos-owl-nav-bg-color);
  border-top: solid 0 transparent;
  border-left: solid 10px transparent;
  right: 100%;
}
.owl-nav .owl-next:hover:after {
  border-bottom-color: var(--amotos-color-accent);
}
@media (min-width: 1200px) {
  .owl-nav {
    --amotos-owl-nav-width: 50px;
