.amotos__car-compare {
  max-width: 360px;
  padding: 15px;
  background-color: #f8f8f8;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}
.amotos__car-compare:before {
  content: "";
  display: block;
  width: 25px;
  height: 25px;
  background: #f8f8f8;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  bottom: -20px;
  -webkit-transform: translateY(-50%) rotate(45deg);
  transform: translateY(-50%) rotate(45deg);
}
.amotos__car-compare .car-item-content {
  padding-top: 17px;
}
.amotos__car-compare .car-title {
  white-space: normal;
}

