}
.amotos__account-login-wrap .amotos__show-password {
  cursor: pointer;
}
.amotos__account-login-wrap .input-group-text {
  background-color: transparent;
  border-color: var(--amotos-color-border);
  border-radius: 0;
}
.amotos__account-login-wrap input:-webkit-autofill,
.amotos__account-login-wrap input:-webkit-autofill:hover,
.amotos__account-login-wrap input:-webkit-autofill:focus,
.amotos__account-login-wrap input:-webkit-autofill:active {
